	"wPyG": "Stanoví statistický protokol Pearsonova korelačního koeficientu. Pearsonova korelace je statistická metoda, která se používá k měření vztahu mezi dvěma veličinami. Jejím cílem je zjistit, zda existuje lineární vztah mezi těmito veličinami a jaký je jeho intenzita.\nPearsonova korelace se vypočítá pomocí vzorce, který se nazývá Pearsonův koeficient korelace. Tento koeficient se pohybuje v rozmezí od -1 do 1 a udává, jak silně je mezi veličinami vztah. Pokud je koeficient blízký -1, znamená to, že mezi veličinami je silný negativní vztah, což znamená, že když se hodnota jedné veličiny zvyšuje, hodnota druhé veličiny klesá. Naopak pokud je koeficient blízký 1, znamená to, že mezi veličinami je silný pozitivní vztah, což znamená, že když se hodnota jedné veličiny zvyšuje, hodnota druhé veličiny také roste. Pokud je koeficient blízký 0, znamená to, že mezi veličinami není žádný vztah nebo je vztah velmi slabý.\nPearsonova korelace se používá především k porovnávání dvou kvantitativních veličin, tj. veličin, které jsou měřitelné na škále s přesnými hodnotami (například věk, výška nebo hmotnost). Může se použít k určení, zda existuje vztah mezi těmito veličinami a jaký je jeho charakter. Například může být Pearsonova korelace použita k porovnání věku a hmotnosti a zjistit, zda existuje vztah mezi těmito veličinami a jaký je jeho charakter. Může se také použít k porovnání výsledků dvou různých testů a zjistit, zda existuje vztah mezi výsledky těchto testů. Zdroj: https://chat.openai.com/chat",
	"qFEM": "první proměnná",
	"tpUu": "druhá proměnná",
	"tpUR": "třetí (kontrolní) proměnná",
	"eJTT": "Spearmanův korelační koeficient",
	"jAGi": "Stanoví statistický protokol Spearmanova koeficientu pořadové korelace. Na rozdíl od Pearsonova korelačního koeficientu vychází hodnota testu z pořadí hodnot ve vstupních proměnných.",
	"mgBC": "Kendallova korelace",
	"VOmC": "Stanoví statistický protokol Kendallova korelačního koeficientu Tau-B. Pokud byste tutéž operaci počítali v SPSS, patrně dostanete mírně odlišný výsledek. Podle všeho je to vlivem odlišné citlivosti na desetinná místa u obou systémů. Na interpretaci výsledku by to nicméně zásadní vliv mít nemělo.",
	"xfSf": "parciální korelace",
	"UcfZ": "Parciální korelační koeficient umožňuje zjistit, jaký je vztah mezi dvěma proměnnými, přičemž zohledňuje možný vliv třetí, kontrolní proměnné. Zjednodušeně řečeno je tento test iterací Pearsonova korelačního koeficientu nad kombinacemi třech vstupních proměnných.",
	"AagR": "bodově biseriální korelace",
	"OMiA": "Bodově biseriální korelační koeficient se používá k vyhodnocení vztahu mezi dvěma proměnnými, kde první proměnná je binárního typu (boolean, biseriální) a druhá proměnná je spojitá. Výpočetně je metoda totožná s výpočet Pearsonova korelačního koeficient, s tím rozdílem, že binární hodnota je před výpočtem transformována (hodnota pravda/true = 1, hodnota nepravda/false = 0). Interpretace koeficientu je totožná s interpretací výše uvedeného koeficientu.",
	"YqRh": "Dvouvýběrový t-test",
	"gILL": "Stanoví statistický protokol Studentova t-testu pro dva nezávislé výběry, které jsou definovány vlastní proměnnou (tedy dvěma numerickými vektory). Argumenty tvoří buď dva numerické vektory, nebo jeden numerický a jen faktorový vektor (obvykle text, ale může být i numerický či binární). Pokud je použit jako faktor vektor, který má více než dvě unikátní hodnoty, jsou pro test uvažovány pouze první dvě unikátní nalezené hodnoty (ostatní se ignorují) - v takovém případě je informace o velikosti čistého vzorku nepodstatná, nicméně hladina významnosti, do které velikost vzorku vstupuje, je již založena na čistých případech.",
	"mmXD": "T-test (párový)",
	"kPqo": "Stanoví statistický protokol párového t-testu pro dva závislé výběry. Test se používá pro srovnání dvou závislých výběrů, u kterých je předpoklad normálního rozdělení. Analogickou metodou pro ordinální proměnné (či proměnné, které nemají předpoklad normality) je Wilcoxonův znaménkový test pro dva závislé výběry.",
