	"OCKc": "Ověří, zdali je hodnota typu binárního vektoru. V opačném případě vyvolá chybu.",
	"CPwN": "Odstraní z vektorů prázdné hodnoty, aniž by odstranění řádku v jednom vektoru ovlivnilo jiný vektor.",
