	"fqwd": "Stanoví statistický protokol kontingence. Parametry metody jsou a) řádková proměnná, b) sloupcová proměnná a volitelně c) četnost skupiny a/b (pokud je prázdná, bere se, že četnost průniku je 1). Výstupem metody jspi kromě chí^2 testu testu také koeficienty Cramérovo V a Pearsonovo C, které doplňují popis vztahů mezi proměnnými.",
