	"ppR3": "Implementace argumentu 'factor'",
	"SQSt": "Jako druhý parametr je specifikován faktor, tedy proměnná, dle které se přetransformuje argument vector (případně první vektor, pakliže je zadáno více numerických vektorů).",
	"dKFL": "Ověří, zdali je zadaná hodnota klíčem enumerace (seznamu možných hodnot). Pokud ne, vyhodí chybu.",
	"u5oV": "enumerace",
	"Cumi": "Vstupní argumenty převede na vektory, ze kterých vytvoří matici. Z této matice následně odebere všechny řádky, které alespoň v jedné buňce obsahují prázdnou hodnotu. Vektory z této dceřiné matice poté přepíše původní argumenty, tzn. že vektory vstupují do metody již očištěné.",
	"Jpe0": "Pokud argumenty specifikují faktorovou proměnnou (druhý argument), rozloží se první argument (buďto numerický vektor nebo první vektor v matici, pokud je prvním argumentem matice) dle hodnot faktoru do nové matice. Pokud jsou argumenty bez faktoru (tedy druhý argument je prázdný), vezme všechny numerické vektory z prvního argumentu 'vectors' (matice) a následně odstraní řádky s prázdnými hodnotami. V případě, že není zadán argument 'factor' a argument 'vectors' zároveň obsahuje pouze jediný vektor nebo je sám numerickým vektorem, vyvolá chybu (bez faktoru jsou třeba alespoň dva numerické vektory v prvním argumentu). V dceřinné matici jsou následně vektory očištěny od prázdných hodnot (buněk), avšak nezávisle na sobě, tzn. že vstupní vektory metody nemusí mít stejný počet členů, jako je to běžné u jiných metod (korelací, párovách testů apod.). Tato skutečnost může být v rozporu se statistikou velikosti vzorku (vlastnost sample u tříd VectorAnalysis i MatrixAnalysis), je proto vhodné zohledňovat spíše hodnotu n, případně df u výstupu.",
